:root{
  --color-dark:#27292a;
  --color-darkest:#1f2122;
  --color-light:#fff;
  --color-primary:#ec6090;

  --color-bg-body:var(--color-darkest);

  --distance-primary:50px;
  --padding-primary:var(--distance-primary);
  --margin-primary:var(--distance-primary);
  --border-radius-primary:calc(var(--distance-primary) / 2);

  --font-weight-bold:900;
  --font-weight-semibold:700;
  --font-weight-normal:400;
  --font-family-primary:'Poppins', sans-serif;
}
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;700;900&display=swap');

body{
  background-color: var(--color-bg-body);
  font-family: var(--font-family-primary);
}
ul{
  list-style: none;
  margin: 0;
}
a{
  text-decoration: none;
}
::-webkit-scrollbar{
  background-color: #ddd;
  width: 10px;
  border-radius:   10px;
  
}
::-webkit-scrollbar-thumb{
  background-color: var(--color-primary);
  border-radius:   10px;
  
}
@media screen and (max-width:992px){
  .hero-main .hero-title{
    font-size: 40px;
  }
  .hero-main{
    background: url('./assets/images/banner-bg.jpg') no-repeat top center/cover !important;
  }
  
}
@media screen and (max-width:550px){
  .section-wrapper{
    padding: 20px;
  }
  .section-header{
    text-align: center;
    margin-top: 20px;
  }
  .section-header h4{
    font-size: 25px;
  }
  .section-header span{
    font-size: 25px;
  }
}