.gaming-library-card{
  flex: 1 1 100%;
}
.gaming-library-card ul{
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  border-bottom: 1px solid var(--color-primary);
  padding: calc( var(--padding-primary) / 2) 0;
  align-items: center;
  gap: 15px 10px;
}
.gaming-library-card h4{
  font-size: 1rem;
  font-weight: 700;
}
.gaming-library-card span{
  color:#919191;
}
@media screen and (max-width:992px){
  .gaming-library-card ul li,
  .gaming-library-card ul div{
    flex: 1 25%;
    font-size: 14px;
  }
  
}