.most-popular-item{
  flex: 1 25%;
}
.card-wrapper{
  padding: calc(var(--padding-primary) / 2);
  background-color: var(--color-dark);
  border-radius: calc(var(--border-radius-primary) /2);
  margin: calc(var(--margin-primary) /5);
}
.most-popular-img{
  width: 100%;
  border-radius: calc(var(--border-radius-primary) /2);
  margin-bottom: calc(var(--margin-primary) /2);
}
.most-popular-content{
  display: flex;
  justify-content: space-between;
}
.most-popular-content-title{
  font-size: 15px;
  line-height: 1.5;
}
.most-popular-content-title span{
  color:#919191;
}

.d-flex-align-icons{
  display: flex;
  align-items: normal;
}